.player-container{
    display: flex;
    justify-content: space-between;
    width: var(--player-width);
    min-height: calc(5 * var(--card-space) + var(--hand-height));
    max-height: var(--player-max-height);
    /* height: 50vh; */
    margin-left: calc((var(--hand-width) + 25px) );
}
.winner-banner{
    position: absolute;
    font-weight: bold;
    font-size: 46px;
    padding: 20px;
    border: 1px solid #333;
    border-radius: .375em;
    background: green;
    left: 50%;
    transform: translateX(-50%);
    top: 100px;
    color: #ebebeb;
    box-shadow: 6px 8px black;
    opacity: 0.8;
}
